import { gql, makeVar, useMutation, useReactiveVar } from "@apollo/client";
import { loadTossPayments } from "@tosspayments/sdk";
import { isMobile } from "react-device-detect";
import { v1 } from "uuid";
import { visualPaymentDataVar } from "../Service_visual2/Var_priceData";
import {
  apllicantDataValidate,
  Var_applicantData,
  Var_applicantInputSetting,
} from "./Lib_applicantFormData";

export default function App({ clickable = true }) {
  const selectedPaymentData = useReactiveVar(visualPaymentDataVar).filter(
    (val) => val.clicked === true
  )[0];
  const applicantData = useReactiveVar(Var_applicantData);
  const trimedMail = applicantData[3].state.trim();

  // 결제승인
  const clientKey = process.env.REACT_APP_TOSS_CLIENTKEY
    ? process.env.REACT_APP_TOSS_CLIENTKEY
    : "";
  async function paymentReq() {
    const tossPayments = await loadTossPayments(clientKey);
    const amount = selectedPaymentData.totalPrice_num;
    const orderId = v1();
    const paymentData = {
      amount,
      orderId,
    };
    window.localStorage.setItem(
      "service_paymentData",
      JSON.stringify(paymentData)
    );
    tossPayments.requestPayment("가상계좌", {
      amount,
      orderId,
      orderName: selectedPaymentData.titleRaw,
      successUrl:
        window.location.origin + "/orderSheet/payment-success-virtual-account",
      failUrl: window.location.origin + "/orderSheet/payment-fail",
      customerName: applicantData[1].state,
      customerEmail: trimedMail,
    });
  }

  // 폼체크하고 발송
  const formCheck = () => {
    apllicantDataValidate.map((val, idx) => {
      if (!val(applicantData[idx].state)) {
        const error = Var_applicantInputSetting()[idx].validateError;
        throw error;
      }
    });
    paymentReq();
    window.localStorage.setItem(
      "applicantDataVar",
      JSON.stringify(applicantData)
    );
    window.localStorage.setItem("paymentType", "가상계좌");
  };

  return clickable ? (
    <div
      className="w-max"
      onClick={() => {
        try {
          formCheck();
        } catch (e) {
          alert(e);
        }
      }}
    >
      <div className="flex  text-shadow-md bg-gradient-to-br from-orange-500 to-orange-400 px-24 py-3 rounded-full cursor-pointer text-white w-max text-lg">
        결제하기
      </div>
    </div>
  ) : (
    <div className="w-max">
      <div className="flex  text-shadow-md bg-gradient-to-br  bg-gray-300 px-24 py-3 rounded-full text-white w-max text-lg">
        결제하기
      </div>
    </div>
  );
}
