import { makeVar, useReactiveVar } from "@apollo/client";
import { useEffect, useState } from "react";

export default function App() {
  useEffect(() => {
    const urlLog: Array<string> = JSON.parse(
      window.localStorage.getItem("urlLog") ||
        JSON.stringify(["", "", "", "", ""])
    );
    const url = window.document.location.pathname;
    urlLog.shift();
    urlLog.push(url);
    window.localStorage.setItem("urlLog", JSON.stringify(urlLog));
  }, []);
  return <></>;
}
