import { isMobile } from "react-device-detect";
import useCheckbox from "/home/po-main1-front/src/hooks/useCheckbox";
import { visualPaymentDataVar } from "../Service_visual2/Var_priceData";
import { modalData1, modalData2 } from "./const_modalData";
import Tem_modal1 from "./Tem_modal1";
import { useState } from "react";
import { useReactiveVar } from "@apollo/client";
import Mol_paymentBtn from "./Mol_paymentBtn_card";
import Mol_paymentBtn_virtualAccount from "./Mol_paymentBtn_virtualAccount";

const data1 = { title: <>결제 안내</> };

const modalTemplete = (data) => ({
  button: <span className="mr-2 underline cursor-pointer">{data.title}</span>,
  modal: (
    <div className="p-3">
      <div className=" text-xl font-bold mb-4">{data.title}</div>
      <ul>
        {data.contents.map((val, idx) => (
          <li key={idx}>
            <div className="font-bold mb-2">{val.title}</div>
            <div className="mb-6">{val.p}</div>
          </li>
        ))}
      </ul>
    </div>
  ),
});

const data3 = [
  <>
    <Tem_modal1 data={modalTemplete(modalData1)}></Tem_modal1>
    <span>동의</span>
  </>,
  <>
    <Tem_modal1 data={modalTemplete(modalData2)}></Tem_modal1>
    <span>동의</span>
  </>,
  <>
    <span>구매확인 동의</span>
  </>,
];

export default function App() {
  const agreeCheckbox = useCheckbox(false);

  const data2 = useReactiveVar(visualPaymentDataVar);

  const paymentData = [
    {
      title: "카드 결제",
      selected: true,
      btn: <Mol_paymentBtn />,
    },
    {
      title: "무통장 입금",
      selected: false,
      btn: <Mol_paymentBtn_virtualAccount />,
    },
  ];
  const [paymentDataState, setPaymentDataState] = useState(paymentData);

  const selectPaymentType = (id) => {
    const newPaymentDataState = [...paymentDataState];
    const allFalse = newPaymentDataState.map((val, idx) =>
      idx === id
        ? {
            ...val,
            selected: true,
          }
        : {
            ...val,
            selected: false,
          }
    );

    setPaymentDataState(allFalse);
  };

  return isMobile ? (
    <section className="">
      <div className="w-10/12 mx-auto">
        <h1 className="font-bold text-xl mb-4 ">{data1.title}</h1>
        <ul className="mb-14">
          {data2.map((val, idx) =>
            val.clicked ? (
              <>
                <li key={idx} className=" pb-4 mb-3 border-b-2">
                  <div>
                    {val.specificationSheet.map((val) => (
                      <div className="flex justify-between">
                        <span>{val.title}</span>
                        <span>{val.price}</span>
                      </div>
                    ))}
                  </div>
                </li>
                <div className="flex justify-between">
                  <span className="font-bold text-lg flex items-end">
                    총 결제금액
                  </span>
                  <span className="font-bold text-2xl text-blue-600">
                    {val.totalPrice_only}
                  </span>
                </div>
              </>
            ) : (
              <></>
            )
          )}
        </ul>
      </div>

      <div className="w-10/12 mx-auto ">
        <div className="font-bold text-xl mb-4 ">결제방법</div>
        <div className="flex justify-around">
          {paymentDataState.map((val, idx) => (
            <div
              className="flex items-center cursor-pointer"
              onClick={() => {
                selectPaymentType(idx);
              }}
            >
              <span className="mr-2 text-xl text-orange-500 relative bottom-px ">
                {val.selected ? (
                  <i className="far fa-check-square"></i>
                ) : (
                  <i className="far fa-square"></i>
                )}
              </span>
              {val.title}
            </div>
          ))}
        </div>
      </div>

      <div className=" border-b-2 my-8"></div>
      <div className="w-10/12 mx-auto">
        <div
          {...agreeCheckbox}
          className="w-max flex items-center mb-4 cursor-pointer"
        >
          <span className="mr-2 text-xl text-orange-500 ">
            {agreeCheckbox.value ? (
              <i className="far fa-check-square"></i>
            ) : (
              <i className="far fa-square"></i>
            )}
          </span>
          <span>주문 내용 확인 및 결제 동의</span>
        </div>

        <div></div>

        <ul>
          {data3.map((val, idx) => (
            <li key={idx} className="flex text-orange-500 items-center mb-1">
              {agreeCheckbox.value ? (
                <div className="w-7 text-sm">
                  <i className="fas fa-check"></i>
                </div>
              ) : (
                <div className="w-7"> </div>
              )}
              {val}
            </li>
          ))}
        </ul>

        <div className="mb-10"></div>

        <div className="flex justify-center">
          {agreeCheckbox.value ? (
            paymentDataState.map((val, idx) =>
              val.selected === true ? val.btn : <></>
            )
          ) : (
            <div className="w-max">
              <div className="flex  text-shadow-md bg-gradient-to-br  bg-gray-300 px-24 py-3 rounded-full text-white w-max text-lg">
                결제하기
              </div>
            </div>
          )}
        </div>

        <div className="mb-10"></div>
      </div>
    </section>
  ) : (
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    <section className="">
      <div className="p-7 border-2 mb-3">
        <h1 className="font-bold text-xl mb-6 ">{data1.title}</h1>
        <ul className="">
          {data2.map((val, idx) =>
            val.clicked ? (
              <>
                <li key={idx} className=" pb-4 mb-4 border-b-2">
                  <div>
                    {val.specificationSheet.map((val) => (
                      <div className="flex justify-between mb-2">
                        <span>{val.title}</span>
                        <span className="font-bold">{val.price}</span>
                      </div>
                    ))}
                  </div>
                </li>
                <div className="flex justify-between">
                  <span className="font-bold ">총 결제금액</span>
                  <span className="font-bold text-xl text-blue-600">
                    {val.totalPrice_only}
                  </span>
                </div>
              </>
            ) : (
              <></>
            )
          )}
        </ul>
      </div>

      <div className="p-7 border-2 mb-3 ">
        <div className="font-bold text-xl mb-4 ">결제방법</div>
        <div className="flex">
          {paymentDataState.map((val, idx) => (
            <div
              className="flex items-center cursor-pointer w-1/2"
              onClick={() => {
                selectPaymentType(idx);
              }}
            >
              <span className="mr-2 text-xl text-orange-500 relative bottom-px ">
                {val.selected ? (
                  <i className="far fa-check-square"></i>
                ) : (
                  <i className="far fa-square"></i>
                )}
              </span>
              {val.title}
            </div>
          ))}
        </div>
      </div>

      <div className="p-7 border-2">
        <div
          {...agreeCheckbox}
          className="w-max flex items-center mb-4 cursor-pointer"
        >
          <span className="mr-2 text-xl text-orange-500 ">
            {agreeCheckbox.value ? (
              <i className="far fa-check-square"></i>
            ) : (
              <i className="far fa-square"></i>
            )}
          </span>
          <span>주문 내용 확인 및 결제 동의</span>
        </div>

        <div></div>

        <ul>
          {data3.map((val, idx) => (
            <li key={idx} className="flex text-orange-500 items-center mb-1">
              {agreeCheckbox.value ? (
                <div className="w-7 text-sm">
                  <i className="fas fa-check"></i>
                </div>
              ) : (
                <div className="w-7"> </div>
              )}
              {val}
            </li>
          ))}
        </ul>

        <div className="mb-10"></div>

        <div className="flex justify-center">
          {agreeCheckbox.value ? (
            paymentDataState.map((val, idx) =>
              val.selected === true ? val.btn : <></>
            )
          ) : (
            <div className="w-max">
              <div className="flex  text-shadow-md bg-gradient-to-br  bg-gray-300 px-24 py-3 rounded-full text-white w-max text-lg">
                결제하기
              </div>
            </div>
          )}
        </div>
      </div>
    </section>
  );
}
