import { isMobile } from "react-device-detect";
import Atm_roundOrangeBtn from "./Atm_roundOrangeBtn";

export default function App({ trigger = false }) {
  return isMobile ? (
    <section
      className={` fixed bottom-6 ${""} `}
      style={
        trigger
          ? {
              zIndex: 100,
              bottom: "-100rem",
              opacity: 0,
              transition: "bottom ease 0s 0.3s, opacity ease 0.2s",
            }
          : {
              zIndex: 100,
              bottom: "1.6rem",
              opacity: 100,
              transition: "opacity ease 0.2s",
            }
      }
    >
      <div className="flex w-screen justify-center">
        <Atm_roundOrangeBtn />
      </div>
    </section>
  ) : (
    <section className="flex max-w-5xl mx-auto justify-end">
      <div className="fixed top-2" style={{ zIndex: 100 }}>
        <Atm_roundOrangeBtn />
      </div>
    </section>
  );
}
