import { gql, useMutation, useReactiveVar } from "@apollo/client";
import { useEffect } from "react";
import { useHistory } from "react-router-dom";
import { Var_applicantData } from "../Payment1_1/Lib_applicantFormData";
import { visualPaymentDataVar } from "../Service_visual2/Var_priceData";
import { v1 } from "uuid";
import { useGtm } from "../../../hooks/useGtm";
import { useDataTag } from "../../../hooks/useDataTag";

export default function App({ data }) {
  const history = useHistory();

  // 쿠키파싱
  const newApplicantData = JSON.parse(
    window.localStorage.getItem("applicantDataVar") ||
      JSON.stringify(Var_applicantData())
  );
  Var_applicantData(newApplicantData);

  const serviceVisualClickedData = JSON.parse(
    window.localStorage.getItem("serviceVisualClickedData") ||
      JSON.stringify("1")
  );
  const newVisualPaymentData = visualPaymentDataVar().map((val, idx) =>
    idx === +serviceVisualClickedData
      ? { ...val, clicked: true }
      : { ...val, clicked: false }
  );
  visualPaymentDataVar(newVisualPaymentData);

  // url search 정보에서 데이터 파싱
  const searchArray = history.location.search.replace("?", "").split("&");
  const paymentData = {
    orderId: searchArray[0]?.split("=")[1],
    paymentKey: searchArray[1]?.split("=")[1],
    amount: Number(searchArray[2]?.split("=")[1]),
  };

  // 로컬스토리지에서 결제 데이터 파싱
  const localPaymentData = JSON.parse(
    window.localStorage.getItem("service_paymentData") ||
      JSON.stringify({ orderId: "", amount: 0 })
  );

  const serviceData = useReactiveVar(visualPaymentDataVar).filter(
    (val) => val.clicked === true
  )[0];

  // 메일발송 정보
  const selectedPaymentData = useReactiveVar(visualPaymentDataVar).filter(
    (val) => val.clicked === true
  )[0];
  const applicantData = useReactiveVar(Var_applicantData);

  const urlLog: Array<string> = JSON.parse(
    window.localStorage.getItem("urlLog") ||
      JSON.stringify(["", "", "", "", ""])
  );

  const trimedMail = applicantData[3].state.trim();

  const date = new Date(data.virtualAccount.dueDate);
  // 액션팀에 전송
  const SENDMAIL_TO_ACTIONTEAM = gql`
    mutation {
      sendVAPaymentMailToAction(
        from: "poketing_mail_server@poketing.com"
        to: "action@poketing.com"
        bank:"${data.virtualAccount.bank}"
        virtualAccount:"${data.virtualAccount.accountNumber}"
        deadline:"${`${date.getFullYear().toString().substring(2, 4)}.${
          date.getMonth() + 1
        }.${date.getDate()} ${date.getHours()}:${date.getMinutes()}까지`}"
        totalAmount:"${serviceData.price_total}"
        subject: "[공홈] 무통장 입금 접수내역 전달"
        serviceName:"비주얼 인플루언서 콘텐츠 마케팅"
        planName:"${
          selectedPaymentData.titleRaw +
          "<br>" +
          selectedPaymentData.mainText
            .map((val, idx) => "✔ " + val.title + " " + val.account)
            .join("<br>") +
          "<br><br> 공급가액: " +
          selectedPaymentData.price_ +
          "<br> 부가세: " +
          selectedPaymentData.price_tex +
          "<br> 할인율: " +
          selectedPaymentData.price_dis +
          "<br> 할인액: " +
          selectedPaymentData.price_disprice +
          "<br> 총 결제금액: " +
          selectedPaymentData.price_total
        }"
        brandName:"${applicantData[0].state}"
        name:"${applicantData[1].state}"
        phoneNumber:"${applicantData[2].state}"
        email:"${
          trimedMail +
          "<br><br>" +
          (urlLog[0] === ""
            ? "<br><br>4전 페이지: 로그 확인 불가"
            : "<br><br>4전 페이지: https://poketing.com") +
          urlLog[0] +
          (urlLog[1] === ""
            ? "<br><br>3전 페이지: 로그 확인 불가"
            : "<br><br>3전 페이지: https://poketing.com") +
          urlLog[1] +
          (urlLog[2] === ""
            ? "<br><br>2전 페이지: 로그 확인 불가"
            : "<br><br>2전 페이지: https://poketing.com") +
          urlLog[2] +
          (urlLog[3] === ""
            ? "<br><br>1전 페이지: 로그 확인 불가"
            : "<br><br>1전 페이지: https://poketing.com") +
          urlLog[3] +
          (urlLog[4] === ""
            ? "<br><br>발송 페이지: 로그 확인 불가"
            : "<br><br>발송 페이지: https://poketing.com") +
          urlLog[4]
        }"
      )
    }
  `;
  const [sendMailMutationToActionteam] = useMutation(SENDMAIL_TO_ACTIONTEAM);

  // 클라이언트메일을 클라이언트에 전송
  const SENDMAIL_TO_CLIENT = gql`
    mutation {
      sendVAPaymentMailToClient(
        from: "action@poketing.com"
        to: "${trimedMail}"
        bank:"${data.virtualAccount.bank}"
        virtualAccount:"${data.virtualAccount.accountNumber}"
        deadline:"${`${date.getFullYear().toString().substring(2, 4)}.${
          date.getMonth() + 1
        }.${date.getDate()} ${date.getHours()}:${date.getMinutes()}까지`}"
        totalAmount:"${serviceData.price_total}"
        subject: "[포케팅] 접수하신 캠페인의 무통장 입금 정보를 안내해드립니다."
        serviceName:"비주얼 인플루언서 콘텐츠 마케팅"        
        planName:"${
          selectedPaymentData.titleRaw +
          "<br>" +
          selectedPaymentData.mainText
            .map((val, idx) => "✔ " + val.title + " " + val.account)
            .join("<br>")
        }"
        brandName:"${applicantData[0].state}"
        name:"${applicantData[1].state}"
        phoneNumber:"${applicantData[2].state}"
        email:"${trimedMail}"
      )
    }
  `;
  const [sendMailMutationToClient1] = useMutation(SENDMAIL_TO_CLIENT);

  // 클라이언트메일을 액션팀에 전송
  const SENDMAIL_TO_CLIENT_TO_ACTIONTEAM = gql`
    mutation {
      sendVAPaymentMailToClient(
        from: "poketing_mail_server@poketing.com"
        to: "action@poketing.com"
        bank:"${data.virtualAccount.bank}"
        virtualAccount:"${data.virtualAccount.accountNumber}"
        deadline:"${`${date.getFullYear().toString().substring(2, 4)}.${
          date.getMonth() + 1
        }.${date.getDate()} ${date.getHours()}:${date.getMinutes()}까지`}"
        totalAmount:"${serviceData.price_total}"
        subject: "[포케팅] 접수하신 캠페인의 무통장 입금 정보를 안내해드립니다."
        serviceName:"비주얼 인플루언서 콘텐츠 마케팅"        
        planName:"${
          selectedPaymentData.titleRaw +
          "<br>" +
          selectedPaymentData.mainText
            .map((val, idx) => "✔ " + val.title + " " + val.account)
            .join("<br>")
        }"
        brandName:"${applicantData[0].state}"
        name:"${applicantData[1].state}"
        phoneNumber:"${applicantData[2].state}"
        email:"${trimedMail}"
      )
    }
  `;
  const [sendMailMutationToClient2] = useMutation(
    SENDMAIL_TO_CLIENT_TO_ACTIONTEAM
  );

  const gtm1 = useGtm({
    eventName: "purchase",
    params: {
      content_category: "비주얼 인플루언서 콘텐츠 마케팅",
      content_name: selectedPaymentData.titleRaw,
      value: selectedPaymentData.totalPrice_num,
      currency: "KRW",
      transaction_id: v1(),
      items: [
        {
          item_id: selectedPaymentData.titleRaw,
          item_name: selectedPaymentData.titleRaw,
          price: selectedPaymentData.totalPrice_num,
          quantity: 1,
        },
      ],
    },
  });

  const ga1 = useDataTag({
    event: "transaction",
    ecommerce: {
      currencyCode: "KRW",
      purchase: {
        actionField: {
          id: v1(),
          revenue: selectedPaymentData.totalPrice_num,
        },
        products: [
          {
            id: selectedPaymentData.titleRaw,
            name: selectedPaymentData.titleRaw,
            price: selectedPaymentData.totalPrice_num,
            quantity: 1,
          },
        ],
      },
    },
  });

  useEffect(() => {
    visualPaymentDataVar(newVisualPaymentData);
    sendMailMutationToActionteam();
    sendMailMutationToClient1();
    sendMailMutationToClient2();

    gtm1();
    ga1();

    window.localStorage.setItem("paymentReq", JSON.stringify(data));

    history.replace("/orderSheet/payment-deposit-information");
  }, []);

  return <></>;
}
