import { useReactiveVar } from "@apollo/client";
import { useEffect } from "react";
import { isMobile } from "react-device-detect";
import { useMutation, useQuery } from "react-query";
import { useHistory } from "react-router-dom";
import { Var_applicantData } from "../Payment1_1/Lib_applicantFormData";
import Payment1_2 from "../Payment1_2";
import PaymentFail from "../PaymentFail";
import { visualPaymentDataVar } from "../Service_visual2/Var_priceData";
import Hk_paySuccess from "./Hk_paySuccess";
import ReactLoading from "react-loading";

// url search 정보에서 데이터 파싱
const queryStringToObject = (query) => {
  return JSON.parse(
    '{"' + decodeURI(query.replace(/&/g, '","').replace(/=/g, '":"')) + '"}'
  );
};

// 객체를 쿼리 스트링으로
const objectToQueryString = function (object) {
  var str: Array<string> = [];
  for (var p in object)
    if (object.hasOwnProperty(p)) {
      str.push(encodeURIComponent(p) + "=" + encodeURIComponent(object[p]));
    }
  return str.join("&");
};

export default function App() {
  // 쿠키파싱
  const newApplicantData = JSON.parse(
    window.localStorage.getItem("applicantDataVar") ||
      JSON.stringify(Var_applicantData())
  );
  Var_applicantData(newApplicantData);

  const serviceVisualClickedData = JSON.parse(
    window.localStorage.getItem("serviceVisualClickedData") ||
      JSON.stringify("1")
  );
  const newVisualPaymentData = visualPaymentDataVar().map((val, idx) =>
    idx === +serviceVisualClickedData
      ? { ...val, clicked: true }
      : { ...val, clicked: false }
  );
  visualPaymentDataVar(newVisualPaymentData);

  const history = useHistory();
  const paymentData = queryStringToObject(
    history.location.search.replace("?", "")
  );

  // 로컬스토리지에서 결제 데이터 파싱
  const localPaymentData = JSON.parse(
    window.localStorage.getItem("service_paymentData") ||
      JSON.stringify({ orderId: "", amount: 0 })
  );

  // 아이템/유저 정보 파싱
  const selectedPaymentData = useReactiveVar(visualPaymentDataVar).filter(
    (val) => val.clicked === true
  )[0];
  const applicantData = useReactiveVar(Var_applicantData);
  const trimedMail = applicantData[3].state.trim();

  const paymentType = window.localStorage.getItem("paymentType");

  const paymentQueryData: object = {
    ...paymentData,
    brandName: applicantData[0].state,
    name: applicantData[1].state,
    phoneNumber: applicantData[2].state,
    email: trimedMail,
    itemName: selectedPaymentData.titleRaw,
    paymentType,
  };

  const paymentMutation = useMutation("paymentReq", () => {
    return fetch(
      process.env.REACT_APP_BACKEND +
        "/payment/virtual-account?" +
        objectToQueryString(paymentQueryData),
      {
        method: "POST",
      }
    ).then((response) => response.json());
  });

  useEffect(() => {
    paymentMutation.mutate();
  }, []);

  // We can assume by this point that `isSuccess === true`
  return (
    <>
      {paymentMutation.isLoading ? (
        <>
          <div className="flex justify-center items-center h-80">
            <ReactLoading type={"bubbles"} color={"rgb(249, 115, 22)"} />
          </div>
        </>
      ) : paymentMutation?.data?.statusCode === 200 ? (
        <>
          <Hk_paySuccess data={paymentMutation?.data} />
        </>
      ) : (
        <>
          {paymentMutation?.data ? (
            <PaymentFail data={paymentMutation?.data} />
          ) : (
            <></>
          )}
        </>
      )}
    </>
  );
}
