import { useReactiveVar } from "@apollo/client";
import { isMobile } from "react-device-detect";
import { Var_applicantData } from "../Payment1_1/Lib_applicantFormData";
import { visualPaymentDataVar } from "../Service_visual2/Var_priceData";
import { CopyToClipboard } from "react-copy-to-clipboard";
import toast, { Toaster } from "react-hot-toast";
import Mol_goToDeposit from "./Mol_goToDeposit";

export default function App() {
  const responseData = JSON.parse(
    window.localStorage.getItem("paymentReq") || JSON.stringify("")
  );
  const serviceData = useReactiveVar(visualPaymentDataVar).filter(
    (val) => val.clicked === true
  )[0];

  const notify = () => toast("복사되었습니다.");

  const date = new Date(responseData.virtualAccount.dueDate);

  const data1 = {
    icon: (
      <>
        <img src={require("./알림.png").default} alt="알림" />
      </>
    ),
    title: <>결제를 완료해주세요!</>,
    h2: (
      <>
        결제 완료 후 캠페인 진행과 관련된 <br />
        안내 메일이 제공됩니다.
      </>
    ),
    detail: [
      {
        title: "가상 계좌",
        data: (
          <div className="flex">
            <span className=" underline">
              {responseData.virtualAccount.accountNumber}
            </span>
            <div
              className="ml-2 border border-gray-400 rounded text-sm text-gray-500 px-1 "
              onClick={notify}
            >
              <CopyToClipboard text={responseData.virtualAccount.accountNumber}>
                <button>복사</button>
              </CopyToClipboard>

              <Toaster
                containerStyle={{
                  top: 200,
                  left: 20,
                  bottom: 20,
                  right: 20,
                }}
              />
            </div>
          </div>
        ),
      },
      { title: "은행", data: <>{responseData.virtualAccount.bank}</> },
      { title: "예금주", data: <>5일의휴일</> },
      {
        title: "입금 기한",
        data: (
          <>{`${date.getFullYear().toString().substring(2, 4)}.${
            date.getMonth() + 1
          }.${date.getDate()} ${date.getHours()}:${date.getMinutes()}까지`}</>
        ),
      },
      {
        title: "입금 금액",
        data: (
          <span className=" text-blue-600 font-bold text-xl">
            {serviceData.totalPrice_only}
          </span>
        ),
      },
    ],
    notification: (
      <span>
        <b>
          ※ 작성해주신 이메일로 무통장 입금 <br /> 정보 안내 메일을
          발송해드렸습니다. :)
        </b>
        <br />
        메일함에서 보이지 않는 경우 <br /> 스팸 메일함을 확인해 주세요!
      </span>
    ),
  };

  return isMobile ? (
    <section className="">
      <div className="flex justify-center mb-5">{data1.icon}</div>
      <div className="flex justify-center text-center font-bold text-orange-500 underline text-2xl mb-4">
        {data1.title}
      </div>
      <div className=" text-center mb-6">{data1.h2}</div>
      <div className=" max-w-xs mx-auto mb-6 bg-gray-50 rounded  px-4 pt-6 pb-3">
        {data1.detail.map((val, idx) => (
          <div className="flex mb-4 items-end ">
            <div className="w-4/12 mr-1 text-gray-500 ">{val.title}</div>
            <div className="w-8/12">{val.data}</div>
          </div>
        ))}
      </div>
      <div className="w-11/12 max-w-xs mx-auto mb-10">
        <div className=" bg-orange-500 rounded-md text-white text-shadow-sm px-4 py-3">
          {data1.notification}
        </div>
      </div>
      <div className="flex justify-center">
        <Mol_goToDeposit />
      </div>
    </section>
  ) : (
    <section className="">
      <div className="flex justify-center mb-5">{data1.icon}</div>
      <div className="flex justify-center text-center font-bold text-orange-500 underline text-2xl mb-4">
        {data1.title}
      </div>
      <div className=" text-center mb-6">{data1.h2}</div>
      <div className=" max-w-sm mx-auto mb-6 bg-gray-50 rounded  px-4 pt-6 pb-3">
        {data1.detail.map((val, idx) => (
          <div className="flex mb-4 items-end ">
            <div className="w-4/12 mr-1 text-gray-500 ">{val.title}</div>
            <div className="w-8/12">{val.data}</div>
          </div>
        ))}
      </div>
      <div className=" max-w-sm mx-auto mb-10 text-center">
        <div className=" bg-orange-500 rounded-md text-white text-shadow-sm px-4 py-3">
          {data1.notification}
        </div>
      </div>
      <div className="flex justify-center">
        <Mol_goToDeposit />
      </div>
    </section>
  );
}
