import { useState } from "react";
import Org_2checkBill from "../Payment1_2/Org_2checkBill";
import { isMobile } from "react-device-detect";
const data1 = {
  title: (
    <>
      결제 대기 중인 <br />
      항목을 확인하세요
    </>
  ),
};

export default function App() {
  const [openState, setOpenState] = useState(false);
  const openStateToggle = () => {
    setOpenState(!openState);
  };
  return (
    <>
      {isMobile ? (
        // 모바일
        // 모바일
        // 모바일
        <section className=" ">
          <div
            className=" border-t-4 border-b-4 py-2"
            onClick={() => {
              openStateToggle();
            }}
          >
            <div className="w-11/12 max-w-xs mx-auto flex justify-between items-center cursor-pointer">
              <div className=" text-lg text-gray-500">의뢰내역 확인</div>
              <div className="mr-1 text-gray-500">
                {openState ? (
                  <i className="fas fa-chevron-up"></i>
                ) : (
                  <i className="fas fa-chevron-down"></i>
                )}
              </div>
            </div>
          </div>
          {openState ? <Org_2checkBill data={data1} /> : <></>}
        </section>
      ) : (
        // 피씨
        // 피씨
        // 피씨
        <section className=" ">
          <Org_2checkBill data={data1} />
        </section>
      )}
    </>
  );
}
