import { useEffect } from "react";
import { useHistory } from "react-router";

function useRouteToTop() {
  const history = useHistory();
  useEffect(() => {
    history.listen(() => {
      window.scrollTo(0, 0);
      // setTimeout(() => {
      //   window.scrollTo(0, 0);
      // }, 0);
    });
  }, [history]);

  return null;
}

export default useRouteToTop;
